import React from "react";
import styled from "styled-components";
import ProfileImage from "../../assets/logos/logo1.png";

const ProfileContainer = styled.div`
  margin-top: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
`;

const ProfileImg = styled.img`
  max-height: 5rem;
  border-radius: 20%;
  margin-bottom: 3rem;
`;

const Profile = () => {
  return (
    <ProfileContainer>
      <ProfileImg src={ProfileImage} />
    </ProfileContainer>
  );
};

export default Profile;
