import React ,{useState,useEffect} from "react";
import styled from "styled-components";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import DashboardImg from "../assets/images/dashboard1.png";
import axios from 'axios';
import Sidebar from "../components/Sidebar/Sidebar";
require('dotenv').config()

const WelcomeContainer = styled.div`
  width: auto;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 16rem;
  padding: 1rem;
  font-weight: 700;
  font-size: 2rem;
  background: url(${DashboardImg}) center/cover no-repeat border-box;
`;
const ContentWrapper = styled.div`
  height: 100%;
  width: auto;
  color: #27af31;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  h1 {
    color: #40b65f;
  }
`;

const CardWrapper = styled.div`
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
`;

const useStyles = makeStyles({
  root: {
    maxWidth: 375,
    minWidth: 275,
    margin: "0 1rem",
    background:
      "linear-gradient(180deg,rgba(203, 203 ,203,0.8),rgba(34 ,176, 71,1))",
  },
  bullet: {
    display: "inline-block",
    margin: "0 2px",
    transform: "scale(0.8)",
  },
  title: {
    fontSize: 25,
    color: "#282828",
    fontWeight: 700,
  },
  pos: {
    marginBottom: 12,
  },
});

const Welcome = () => {
  const classes = useStyles();
  const [offer, setOffer] = useState(null);
  const[menu,setMenu] =useState(null);
  const url = process.env.REACT_APP_API +"/offer";
  const menu_url=process.env.REACT_APP_API +"/menu";

  const fetchOffer = async () => {
    axios.get(url).then((response) => {
      if (response.data) {
        setOffer(response.data.length);
      } else {
      }
    });
  };

  const fetchMenu = async () => {
    axios.get(menu_url).then((response) => {
      if (response.data) {
        setMenu(response.data.length);
      } else {
      }
    });
  };


  useEffect(() => {
    fetchMenu();
    fetchOffer();
  },[1]);

  return (
<>
<Sidebar />
    <WelcomeContainer>
      <ContentWrapper>
        <h1>Welcome to VEGPOINT</h1>
        <CardWrapper>
          <Card className={classes.root}>
            <CardContent>
              <Typography
                className={classes.title}
                color="textPrimary"
                gutterBottom
              >
                Our Menu
              </Typography>
              <Typography className={classes.pos} color="textSecondary">
                Total
              </Typography>
              <Typography variant="body1" component="p">
                {menu} Items
                <br />
              </Typography>
            </CardContent>
          </Card>

          <Card className={classes.root}>
            <CardContent>
              <Typography
                className={classes.title}
                color="textPrimary"
                gutterBottom
              >
                Offers
              </Typography>
              <Typography className={classes.pos} color="textSecondary">
                Total
              </Typography>
              <Typography variant="body1" component="p">
                {offer} Items
                <br />
              </Typography>
            </CardContent>
          </Card>
        </CardWrapper>
      </ContentWrapper>
    </WelcomeContainer>
    </>
  );
};

export default Welcome;
