import React, { useState, useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";
import Button from "@material-ui/core/Button";
import styled from "styled-components";
import axios from "axios";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faEdit, faTrash , faBoxOpen} from "@fortawesome/free-solid-svg-icons";
import Modal from "react-modal";
import TextField from "@material-ui/core/TextField";
import Select from "@material-ui/core/Select";
import InputLabel from "@material-ui/core/InputLabel";
import FormControl from "@material-ui/core/FormControl";
import { Link } from "react-router-dom";
import Sidebar from "../components/Sidebar/Sidebar";
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css'; // Import css

import { css } from "@emotion/core";
import PulseLoader from "react-spinners/PulseLoader";

require('dotenv').config()

// Can be a string as well. Need to ensure each key-value pair ends with ;
const override = css`
  display: block;
 
  margin: auto;
  transform: "translate(-50%, -50%)",

  
 
`;
const customStyles = {
  content: {
    top: "50%",
    left: "50%",
    right: "auto",
    bottom: "auto",
    marginRight: "-50%",
    transform: "translate(-50%, -50%)",
  },
};

const OffersContainer = styled.div`
  width: auto;
  height: 100vh;
  display: flex;
  flex-direction: column;
  margin-top: 5rem;
  align-items: start;
  margin-left: 16rem;
  padding: 1rem;

  .MuiButton-containedPrimary {
    width: 150px;
    margin: 1rem 0;
    background-color: #40b65f;
  }
  .MuiButton-containedPrimary:hover {
    background-color: #2a9432;
  }

  .MuiTableCell-head {
    font-weight: 700;
    color:#5e5b5b;

  }
`;

const ActionItem = styled.div`
  display: flex;
  justify-content: flex-end;
  align-items: center;
  font-size: 1.1rem;
  cursor: pointer;
  .edit {
    margin: 0 0.5rem;
    color: #43ca53;
    text-decoration: none;
  }
  .delete {
    color: #d33e23;
  }
`;

const FormContainer = styled.div`
  form {
    padding: 2.5rem;
    display: flex;
    flex-direction: column;
  }
  .MuiFormControl-root {
    border: 0;
    margin: 0.5rem 0;
    display: inline-flex;
    padding: 0;
    position: relative;
    min-width: 0;
    flex-direction: column;
    vertical-align: top;
  }
`;

const BtnContainer = styled.div`
  display: flex;
  justify-content: space-around;
`;

const useStyles = makeStyles({
  table: {
    minWidth: 750,
  },
});

export default function Time() {
  const [offer, setOffer] = useState(null);
  const url = process.env.REACT_APP_API +"/offer";

  const fetchData = async () => {
    axios.get(url).then((response) => {
      if (response.data) {
        setOffer(response.data);
      } else {
      }
    });
  };



  var subtitle;
  const [modalIsOpen, setIsOpen] = React.useState(false);
  const [name, setname] = useState(null);
  const [id, setid] = useState(null);
  const [status, setStatus] = useState(null);
  const [desc_en, setDesc_en] = useState(null);
  const [desc_fi, setDesc_fi] = useState(null);
  let [loading, setLoading] = useState(false);
  const [nameError, setnameError] = useState(null);
  const [desc_enError, setdesc_enError] = useState(null);
  const [desc_fiError, setdesc_fiError] = useState(null);
  const [statusError, setstatusError]= useState(null);
 
  function openModal() {
    setIsOpen(true);
  }

  


  function afterOpenModal() {
    // references are now sync'd and can be accessed.
    subtitle.style.color = "#268B93";
  }

  function closeModal() {
    setIsOpen(false);
    setname(null);
    setDesc_en(null);
    setDesc_fi(null);
    setStatus(null);
    setid(null);
    setstatusError(null);
  }

  const editmodal = (id,name, desc_en,desc_fi,status) => {
    setid(id);
    setname(name);
    setDesc_en(desc_en);
    setDesc_fi(desc_fi);
    setStatus(status);
    openModal();
    
  };

  function validate(){
    const name = document.getElementById("name").value;
    const status = document.getElementById("status").value;
    const desc_en = document.getElementById("desc_en").value;
    const desc_fi = document.getElementById("desc_fi").value;


    if(!name){
      setnameError("This field is required.")
    }else{
      setnameError(null);
    }
  
    if(!desc_en){
      setdesc_enError("This field is required.")
    }else{
      setdesc_enError(null);
    }
    if(!desc_fi){
      setdesc_fiError("This field is required.")
    }else{
      setdesc_fiError(null);
    }
    if(!status){
      setstatusError("This field is required.")
    }else{
      setstatusError(null);
    }

   
    if(name && desc_en && desc_fi && status){
      setLoading(true);
      handleNewAdd();
    }
   
  }

  function handleNewAdd() {
    const name = document.getElementById("name").value;
    const status = document.getElementById("status").value;
    const desc_en = document.getElementById("desc_en").value;
    const desc_fi = document.getElementById("desc_fi").value;

    const url = process.env.REACT_APP_API +"/offer";
    

    if (id) {
      axios.put(url+"/"+id, { id: id, name: name, description_en: desc_en,description_fi: desc_fi, status: status })
      .then((response) => {

        confirmAlert({
          title: 'Response messge',
          message: response.data,
          buttons: [
            {
              label: 'Okay',
              onClick: () => null
            }
           
          ]
        });
        fetchData();
        setLoading(false);
      })
      .catch((error) => {});
    } else {
      axios.post(url, { name: name, description_en: desc_en,description_fi: desc_fi, status: status })
      .then((response) => {

        confirmAlert({
          title: 'Response messge',
          message: response.data,
          buttons: [
            {
              label: 'Okay',
              onClick: () => null
            }
           
          ]
        });
        fetchData();
        setLoading(false);
      })
      .catch((error) => {});
    }

    closeModal();
  }

  const menu_url = process.env.REACT_APP_API +"/menu"

  const menuoptions = [];
  const fetchMenu = async () => {
    axios.get(menu_url).then((response) => {
      if (response.data) {

       
        for (var result in response.data){
          
          menuoptions.push({
            value: response.data[result].id,
            label: response.data[result].name
          });
        }
        
      } else {
      }
    });
  };



  const confirmDelete = (id) => {
    setLoading(true);
    const url = process.env.REACT_APP_API +"/offer/"+id;
    axios.delete(url).then((response) => {
      setLoading(false);
      confirmAlert({
        title: 'Response messge',
        message: response.data,
        buttons: [
          {
            label: 'Okay',
            onClick: () => null
          }
         
        ]
      });
      
      fetchData();
     
    })
    .catch((error) => {
      console.log(error.message);
      setLoading(false);
     
    });


  }
  const handleDelete = (id) => {

    confirmAlert({
      title: 'Confirm to submit',
      message: 'Are you sure to delete this.',
      buttons: [
        {
          label: 'Yes',
          onClick: () => confirmDelete(id)
        },
        {
          label: 'No',
          onClick: () => null
        }
      ]
    });
   
  };


 

  const classes = useStyles();


  useEffect(() => {
    fetchMenu();
    fetchData();
  },[2]);


  return (
    <>
    <Sidebar />
    <OffersContainer>
      <h1>Offers</h1>
      <br />
      <Button variant="contained" color="primary" onClick={openModal}>
        New Offer
      </Button>
      <Modal
        isOpen={modalIsOpen}
        onAfterOpen={afterOpenModal}
        onRequestClose={closeModal}
        style={customStyles}
        contentLabel="Offer Modal"
      >
        <h2
          ref={(_subtitle) => (subtitle = _subtitle)}
          style={{ textAlign: "center" }}
        >
          Add/Edit Offer
        </h2>
        <FormContainer>
          <form>
          <div style={{ color:"red"}}>{nameError}</div>
            <TextField
              id="name"
              defaultValue={name}
              label="Offer Name"
              variant="outlined"
            />
 
           <div style={{ color:"red"}}>{desc_enError}</div>
            <TextField
              id="desc_en"
              defaultValue={desc_en}
              label="Description in English"
              variant="outlined"
            />
             <div style={{ color:"red"}}>{desc_fiError}</div>
             <TextField
              id="desc_fi"
              defaultValue={desc_fi}
              label="Description in Finland"
              variant="outlined"
            />

            <FormControl variant="outlined" className={classes.formControl}>
              <InputLabel htmlFor="outlined-age-native-simple">
                Status
              </InputLabel>
              <div style={{ color:"red"}}>{statusError}</div>
              <Select native id="status" defaultValue={status} label="Status">
                <option aria-label="None" value="" />
                <option value={true}>True</option>
                <option value={false}>False</option>
              </Select>
            </FormControl>
          </form>
        </FormContainer>
        <BtnContainer>
          {" "}
          <Button variant="contained" color="primary" onClick={validate}>
            Submit
          </Button>
          <Button variant="contained" color="secondary" onClick={closeModal}>
            close
          </Button>
        </BtnContainer>
      </Modal>
      <TableContainer component={Paper}>
      <PulseLoader color={'#43ca53'} loading={loading} css={override} size={15} />

        <Table className={classes.table} aria-label="simple table">
          <TableHead>
            <TableRow>
              <TableCell>Offer Name</TableCell>
              <TableCell align="right">Status</TableCell>
              <TableCell align="right">Action</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {offer &&
              offer.map((offer, index) => {
                return (
                  <TableRow key={index}>
                    <TableCell component="th" scope="row">
                      {offer.name}
                    </TableCell>
                    <TableCell align="right">{offer.status}</TableCell>
                    <TableCell align="right">
                      <ActionItem>
                        <div className="edit">
                          <Link
                            to={{ pathname: "/admin/offerentry", 
                            offerProps: {
                              offerid: offer.id
                             
                             } }}
                            className="edit"
                          >
                            <FontAwesomeIcon
                              
                              icon={faBoxOpen}
                            />

                          </Link>
                        </div>
                        <div className="delete">
                          <FontAwesomeIcon
                            icon={faTrash}
                            onClick={() => handleDelete(offer.id)}
                          />
                        </div>
                        <div className="edit">
                          <FontAwesomeIcon
                            icon={faEdit}
                            onClick={() => editmodal(offer.id,offer.name,offer.description_en,offer.description_fi,offer.status)}
                          />
                        </div>
                      </ActionItem>
                    </TableCell>
                  </TableRow>
                );
              })}
          </TableBody>
        </Table>
      </TableContainer>
    </OffersContainer>
    </>
  );
}

