import React, { useState, useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";
import styled from "styled-components";
import axios from "axios";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faEdit, faTrash } from "@fortawesome/free-solid-svg-icons";
import Modal from "react-modal";
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css'; // Import css

import TextField from "@material-ui/core/TextField";
import TextareaAutosize from "@material-ui/core/TextareaAutosize";
import Select from "@material-ui/core/Select";
import InputLabel from "@material-ui/core/InputLabel";
import FormControl from "@material-ui/core/FormControl";
import Button from "@material-ui/core/Button";

import { css } from "@emotion/core";
import PulseLoader from "react-spinners/PulseLoader";
require('dotenv').config()
// Can be a string as well. Need to ensure each key-value pair ends with ;
const override = css`
  display: block;
 
  margin: auto;
  transform: "translate(-50%, -50%)",

  
 
`;
const customStyles = {
  content: {
    top: "50%",
    left: "50%",
    right: "auto",
    bottom: "auto",
    marginRight: "-50%",
    transform: "translate(-50%, -50%)",
  },
};

const DinnerContainer = styled.div`
  height: auto;
  margin-top: 2rem;
  align-items: start;
`;
const ActionItem = styled.div`
  display: flex;
  justify-content: flex-end;
  align-items: center;
  font-size: 1.1rem;
  cursor: pointer;
  .edit {
    margin: 0 0.5rem;
    color: #43ca53;
    text-decoration: none;
  }
  .delete {
    color: #d33e23;
  }
`;

const FormContainer = styled.div`
  form {
    padding: 2.5rem;
    display: flex;
    flex-direction: column;
  }
  #desc {
    border: 1px solid #b8b8b8;
    border-radius: 4px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    font-family: "Roboto";
    font-size: 1rem;
    padding-left: 0.8rem;
    padding-top: 0.5rem;
  }
  .MuiFormControl-root {
    border: 0;
    margin: 1rem 0;
    display: inline-flex;
    padding: 0;
    position: relative;
    min-width: 0;
    flex-direction: column;
    vertical-align: top;
  }
`;
const BtnContainer = styled.div`
  display: flex;
  justify-content: space-around;
`;

export default function MenuCategory({ foodUrl }) {
  const [menuItem, setMenuItem] = useState(null);
  const url = foodUrl;

  const fetchData = async () => {
    axios.get(url).then((response) => {
      if (response.data) {
        console.log(response.data);
        setMenuItem(response.data);
      } else {
      }
    });
  };

  useEffect(() => {
    fetchData();
  },[1]);

  var subtitle;
  const [modalIsOpen, setIsOpen] = React.useState(false);
  const [name, setname] = useState(null);
  const [desc_en, setdesc_en] = useState(null);
  const [desc_fi, setdesc_fi] = useState(null);
  const [pic, setpic] = useState(null);
  const [price, setprice] = useState(null);
  const [type, settype] = useState(null);
  const [subtype, setsubtype] = useState(null);
  const [id, setid] = useState(null);
  let [loading, setLoading] = useState(false);
  const [typeError, setTypeError] = useState(null);
  const [nameError, setnameError] = useState(null);
  const [desc_enError, setdesc_enError] = useState(null);
  const [desc_fiError, setdesc_fiError] = useState(null);
  const [priceError, setpriceError]= useState(null);
  const [picError, setpicError]= useState(null);

  function openModal() {
    setIsOpen(true);
  }

  function afterOpenModal() {
    // references are now sync'd and can be accessed.
    subtitle.style.color = "#f00";
  }

  function closeModal() {
    setIsOpen(false);
    setname(null);
    setdesc_en(null);
    setdesc_fi(null);
    setpic(null);
    setprice(null);
    setid(null);
    settype(null);
    setsubtype(null);
    setnameError(null);
    setdesc_enError(null);
    setdesc_fiError(null);
    setTypeError(null);
    setpicError(null);
    setpriceError(null);
  }

  const editmodal = (id, name, desc_en,desc_fi, price, type, subtype,pic) => {
    setname(name);
    setdesc_en(desc_en);
    setdesc_fi(desc_fi);
    setpic(pic);
    setprice(price);
    settype(type);
    setsubtype(subtype);
    setid(id);
    openModal();
  };

  const fileSelectedHandler = (event) => {
    setpic(event.target.files[0]);
  };


  function validate(){
    const name = document.getElementById("name").value;
    const price = document.getElementById("price").value;
    const desc_en = document.getElementById("desc_en").value;
    const desc_fi = document.getElementById("desc_fi").value;
    const type = document.getElementById("type").value;

    if(!name){
      setnameError("This field is required.")
    }else{
      setnameError(null);
    }
    if(!price){
      setpriceError("This field is required.")
    }else{
      setpriceError(null);
    }
    if(!desc_en){
      setdesc_enError("This field is required.")
    }else{
      setdesc_enError(null);
    }
    if(!desc_fi){
      setdesc_fiError("This field is required.")
    }else{
      setdesc_fiError(null);
    }
    if(!type){
      setTypeError("This field is required.")
    }else{
      setTypeError(null);
    }

    if(!pic){
      setpicError("This field is required.")
    }else{
      setpicError(null);
    }
    if(name && desc_en && desc_fi && type && pic && price){
      setLoading(true);
      handleEdit();
    }
   
  }

  function handleEdit() {
    let name = document.getElementById("name").value;
    let price = document.getElementById("price").value;
    let desc_en = document.getElementById("desc_en").value;
    let desc_fi = document.getElementById("desc_fi").value;
    let type = document.getElementById("type").value;
    let subtype = document.getElementById("subtype").value;

    let url = process.env.REACT_APP_API +"/menu/"+id;

    const config = {
      headers: {
        "content-type": "multipart/form-data",
      },
    };

 
    if (id) {
      const formData = new FormData();
      formData.append("name", name);
      formData.append("price", price);
      formData.append("description_en", desc_en);
      formData.append("description_fi", desc_fi);
      formData.append("type", type);
      formData.append("subtype",subtype);
      formData.append("pic", pic);
      formData.append("id", id);
        
    axios
        .post(url, formData, config)
        .then((response) => {

          confirmAlert({
            title: 'Response messge',
            message: response.data,
            buttons: [
              {
                label: 'Okay',
                onClick: () => null
              }
             
            ]
          });
          fetchData();
          setLoading(false);
        })
        .catch((error) => {
          setLoading(false);
          
        });
    

    }

   closeModal();
  }

  const confirmDelete = (id) => {
    setLoading(true);
    const url = process.env.REACT_APP_API +"/menu";
    axios.delete(url, { data: { id: id } }).then((response) => {

      confirmAlert({
        title: 'Response messge',
        message: response.data,
        buttons: [
          {
            label: 'Okay',
            onClick: () => null
          }
         
        ]
      });
      fetchData();
      setLoading(false);
     
    })
    .catch((error) => {
      setLoading(false);
     
    });


  }
  const handleDelete = (id) => {
 
    confirmAlert({
      title: 'Confirm to submit',
      message: 'Are you sure to delete this.',
      buttons: [
        {
          label: 'Yes',
          onClick: () => confirmDelete(id)
        },
        {
          label: 'No',
          onClick: () => null
        }
      ]
    });
   
  };

  return (
    <DinnerContainer>
      <Modal
        isOpen={modalIsOpen}
        onRequestClose={closeModal}
        style={customStyles}
        contentLabel="Example Modal"
      >
        <h2 style={{ textAlign: "center", color: "#268B93" }}>
          Edit Menu Item
        </h2>
        <FormContainer>
          <form enctype="multipart/form-data">
          <div style={{ color:"red"}}>{nameError}</div>
            <TextField
              id="name"
              defaultValue={name}
              label="Item Name"
              variant="outlined"
            />
            
             <div style={{ color:"red"}}>{desc_enError}</div>
            <TextareaAutosize
              id="desc_en"
              aria-label="minimum height"
              rowsMin={5}
              defaultValue={desc_en}
              placeholder="Item Description"
            />
            
            <br/>
<div style={{ color:"red"}}>{desc_fiError}</div>
          <TextareaAutosize
              id="desc_fi"
              aria-label="minimum height"
              rowsMin={5}
              defaultValue={desc_fi}
              placeholder="Item Description"
            />
            <div style={{ color:"red"}}>{priceError}</div>
            <TextField
              id="price"
              label="Price"
              defaultValue={price}
              variant="outlined"
              
            />
            <FormControl variant="outlined">
              <InputLabel htmlFor="outlined-age-native-simple">
                Type:
              </InputLabel>
              <div style={{ color:"red"}}>{typeError}</div>
              <Select native id="type" defaultValue={type} label="category">
                <option aria-label="None" value="" />
                <option value="soup">Soup</option>
                <option value="appetizer">Appetizer</option>
                <option value="tandooridish">Tandoori Dishes</option>
                <option value="maindish">Main Dishes</option>
                <option value="special">Chef's Special</option>
                <option value="snacks">Snacks</option>
                <option value="rice">Rice</option>
                <option value="bread">Breads</option>
                <option value="beverage">Beverages</option>
                <option value="dessert">Desserts</option>
                <option value="kids">For Kids</option>
              </Select>
            </FormControl>

            <FormControl variant="outlined">
              <InputLabel htmlFor="outlined-age-native-simple">
                SubType:
              </InputLabel>
              <Select native id="subtype"  defaultValue={subtype} label="category">
                <option aria-label="None" value="" />
                <option value="hot">Hot Drink</option>
                <option value="cold">Cold Drink</option>
               
              </Select>
            </FormControl>

           

            <InputLabel htmlFor="outlined-age-native-simple">Icon:</InputLabel>{" "}
            <img src={pic} height="20px" width="20px"></img>
            <div style={{ color:"red"}}>{picError}</div>
            <TextField
              id="pic"
              variant="outlined"
              type="file"
              onChange={fileSelectedHandler}
            />
          </form>
        </FormContainer>
        <BtnContainer>
          {" "}
          <Button variant="contained" color="primary" onClick={validate}>
            Submit
          </Button>
          <Button variant="contained" color="secondary" onClick={closeModal}>
            close
          </Button>
        </BtnContainer>
      </Modal>
      <TableContainer component={Paper}>
      <PulseLoader color={'#43ca53'} loading={loading} css={override} size={15} />

        <Table aria-label="simple table">
          <TableHead>
            <TableRow>
              <TableCell>Name</TableCell>
              <TableCell align="center">Description</TableCell>
              <TableCell align="right">Icon</TableCell>
              <TableCell align="right">Price</TableCell>

              <TableCell align="right">Action</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {menuItem &&
              menuItem.map((menuItem, index) => {
                return (
                  <TableRow key={menuItem.name} key={index}>
                    <TableCell component="th" scope="row">
                      {menuItem.name}
                    </TableCell>
                    <TableCell align="center">{menuItem.description_en}</TableCell>
                    <TableCell align="right">
                      <img height="60" width="60" src={menuItem.pic}></img>
                    </TableCell>
                    <TableCell align="right">{menuItem.price}</TableCell>
                    <TableCell align="right">
                      <ActionItem>
                        <div className="edit">
                          <FontAwesomeIcon
                            icon={faEdit}
                            onClick={() =>
                              editmodal(
                                menuItem.id,
                                menuItem.name,
                                menuItem.description_en,
                                menuItem.description_fi,
                                menuItem.price,
                                menuItem.type,
                                menuItem.subtype,
                                menuItem.pic
                                
                              )
                            }
                          />{" "}
                        </div>
                        <div className="delete">
                          <FontAwesomeIcon
                            icon={faTrash}
                            onClick={() => handleDelete(menuItem.id)}
                          />
                        </div>
                      </ActionItem>
                    </TableCell>
                  </TableRow>
                );
              })}
          </TableBody>
        </Table>
      </TableContainer>
    </DinnerContainer>
  );
}
