import React, { useState } from "react";
import styled from "styled-components";
import { makeStyles } from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import axios from "axios";
import TextField from "@material-ui/core/TextField";
import TextareaAutosize from "@material-ui/core/TextareaAutosize";
import Select from "@material-ui/core/Select";
import InputLabel from "@material-ui/core/InputLabel";
import FormControl from "@material-ui/core/FormControl";
import Modal from "react-modal";
import PropTypes from "prop-types";
import AppBar from "@material-ui/core/AppBar";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import Typography from "@material-ui/core/Typography";
import Box from "@material-ui/core/Box";
import LunchCategory from "./Lunch/LunchCategory";
import Sidebar from "../components/Sidebar/Sidebar";

import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css'; // Import css
import { css } from "@emotion/core";
import PulseLoader from "react-spinners/PulseLoader";
require('dotenv').config()
// Can be a string as well. Need to ensure each key-value pair ends with ;
const override = css`
  display: block;
 
  margin: auto;
  transform: "translate(-50%, -50%)",

  
 
`;
const MenuItemsContainer = styled.div`
  width: auto;
  height: 100vh;
  max-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-left: 16rem;
  padding: 1rem;

  .MuiTableCell-head {
    font-weight: 700;
    color:#5e5b5b;

  }

  .MuiButton-containedPrimary {
    width: 150px;
    margin: 1.5rem 0;
    background-color: #40b65f;
  }
  .MuiButton-containedPrimary:hover {
    background-color: #2a9432;
  }
  .MuiAppBar-colorPrimary {
    background-color: #40b65f;
  }
  .mui-tab {
    width: 100%;
  }
`;

const FormContainer = styled.div`
  form {
    padding: 2.5rem;
    display: flex;
    flex-direction: column;
  }
  #desc {
    border: 1px solid #b8b8b8;
    border-radius: 4px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    font-family: "Roboto";
    font-size: 1rem;
    padding-left: 0.8rem;
    padding-top: 0.5rem;
  }
  .MuiFormControl-root {
    border: 0;
    margin: 1rem 0;
    display: inline-flex;
    padding: 0;
    position: relative;
    min-width: 0;
    flex-direction: column;
    vertical-align: top;
  }
`;

const BtnContainer = styled.div`
  display: flex;
  justify-content: space-around;
`;

const customStyles = {
  content: {
    top: "50%",
    left: "50%",
    right: "auto",
    bottom: "auto",
    marginRight: "-50%",
    transform: "translate(-50%, -50%)",
  },
};

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    "aria-controls": `simple-tabpanel-${index}`,
  };
}

function LunchContainer() {
  const [modalIsOpen, setIsOpen] = React.useState(false);
  const [pic, setpic] = useState(null);
  let [loading, setLoading] = useState(false);
  const [dayError, setDayError] = useState(null);
  const [nameError, setnameError] = useState(null);
  const [desc_enError, setdesc_enError] = useState(null);
  const [desc_fiError, setdesc_fiError] = useState(null);
  const [priceError, setpriceError]= useState(null);
  const [picError, setpicError]= useState(null);

  function openModal() {
    setIsOpen(true);
  }

  function closeModal() {
    setIsOpen(false);
    setnameError(null);
    setdesc_enError(null);
    setdesc_fiError(null);
    setDayError(null);
    setpicError(null);
    setpriceError(null);
  }

  function validate(){
    const name = document.getElementById("name").value;
    const price = document.getElementById("price").value;
    const desc_en = document.getElementById("desc_en").value;
    const desc_fi = document.getElementById("desc_fi").value;
    const day = document.getElementById("day").value;

    if(!name){
      setnameError("This field is required.")
    }else{
      setnameError(null);
    }
    if(!price){
      setpriceError("This field is required.")
    }else{
      setpriceError(null);
    }
    if(!desc_en){
      setdesc_enError("This field is required.")
    }else{
      setdesc_enError(null);
    }
    if(!desc_fi){
      setdesc_fiError("This field is required.")
    }else{
      setdesc_fiError(null);
    }
    if(!day){
      setDayError("This field is required.")
    }else{
      setDayError(null);
    }

    if(!pic){
      setpicError("This field is required.")
    }else{
      setpicError(null);
    }
    if(name && desc_en && desc_fi && day && pic && price){
      setLoading(true);
      handleAdd();
    }
   
  }
  function handleAdd() {
    const name = document.getElementById("name").value;
    const price = document.getElementById("price").value;
    const desc_en = document.getElementById("desc_en").value;
    const desc_fi = document.getElementById("desc_fi").value;
    const day = document.getElementById("day").value;

    const url = "http://127.0.0.1:4000/menu";

    const config = {
      headers: {
        "content-type": "multipart/form-data",
      },
    };
    const formData = new FormData();
    formData.append("name", name);
      formData.append("price", price);
      formData.append("description_en", desc_en);
      formData.append("type", 'lunch');
      formData.append("description_fi", desc_fi);
      formData.append("pic", pic);
      formData.append("day",day);

    axios
      .post(url, formData, config)
      .then((response) => {

        confirmAlert({
          title: 'Response messge',
          message: response.data,
          buttons: [
            {
              label: 'Okay',
              onClick: () => null
            }
           
          ]
        });
        
        setLoading(false);
      })
      .catch((error) => {
        setLoading(false);
      });

    closeModal();
  }

  const fileSelectedHandler = (event) => {
    setpic(event.target.files[0]);
  };

  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };
  return (

    <>
    <Sidebar />
    <MenuItemsContainer>
      <h1>Lunch Items</h1>
      <br />
      <Button variant="contained" color="primary" onClick={openModal}>
        Add Items
      </Button>
      <Modal
        isOpen={modalIsOpen}
        onRequestClose={closeModal}
        style={customStyles}
        contentLabel="Example Modal"
      >
        <h2 style={{ textAlign: "center", color: "#268B93" }}>Add Lunch Item</h2>
        <FormContainer>
          <form enctype="multipart/form-data">
          <div style={{ color:"red"}}>{nameError}</div>
            <TextField id="name" label="Item Name" variant="outlined" />
            <div style={{ color:"red"}}>{desc_enError}</div>
            <TextareaAutosize
              id="desc_en"
              aria-label="minimum height"
              rowsMin={5}
            
              lable="English Description"
              variant="outlined"
              placeholder="Lunch Description in English"
            />
 <div style={{ color:"red"}}>{desc_fiError}</div>
            <TextareaAutosize
              id="desc_fi"
              aria-label="minimum height"
              rowsMin={5}
             
              lable="Finland Description"
              variant="outlined"
              placeholder="Lunch Description in Finland"
            />
<div style={{ color:"red"}}>{priceError}</div>
            <TextField
              id="price"
              label="Price"
              variant="outlined"
              type="number"
            />
            <FormControl variant="outlined">
              <InputLabel htmlFor="outlined-age-native-simple">
                Day:
              </InputLabel>
              <div style={{ color:"red"}}>{dayError}</div>
              <Select native id="day"  label="category">
              
                <option value="monday">Monday</option>
                <option value="tuesday">Tuesday</option>
                <option value="wednesday">Wednesday</option>
                <option value="thursday">Thursday</option>
                <option value="friday">Friday</option>
                
              </Select>
            </FormControl>
            <InputLabel htmlFor="outlined-age-native-simple">Icon:</InputLabel>{" "}
            <div style={{ color:"red"}}>{picError}</div>
            <TextField
              id="pic"
              variant="outlined"
              type="file"
              onChange={fileSelectedHandler}
            />
          </form>
        </FormContainer>

        <BtnContainer>
          {" "}
          <Button variant="contained" color="primary" onClick={validate}>
            Submit
          </Button>
          <Button variant="contained" color="secondary" onClick={closeModal}>
            close
          </Button>
        </BtnContainer>
      </Modal>

      <div className="mui-tab">
      <PulseLoader color={'#43ca53'} loading={loading} css={override} size={15} />

        <AppBar position="static">
          <Tabs
            value={value}
            onChange={handleChange}
            aria-label="simple tabs example"
            variant="scrollable"
            scrollButtons="auto"
          >
          
            <Tab label="Monday" {...a11yProps(0)} />
            <Tab label="Tuesday"{...a11yProps(1)} />
            <Tab label="Wednesday" {...a11yProps(2)} />
            <Tab label="Thursday" {...a11yProps(3)} />
            <Tab label="Friday" {...a11yProps(4)} />
           
          </Tabs>
        </AppBar>
       
        <TabPanel value={value} index={0}>
          <LunchCategory foodUrl={process.env.REACT_APP_API +"/menu/get/lunch/monday"} />{" "}
        </TabPanel>
        <TabPanel value={value} index={1}>
          <LunchCategory foodUrl={process.env.REACT_APP_API + "/menu/get/lunch/tuesday"} />{" "}
        </TabPanel>
        <TabPanel value={value} index={2}>
          <LunchCategory foodUrl={process.env.REACT_APP_API +"/menu/get/lunch/wednesday"} />{" "}
        </TabPanel>
        <TabPanel value={value} index={3}>
          <LunchCategory foodUrl={process.env.REACT_APP_API +"/menu/get/lunch/thursday"} />{" "}
        </TabPanel>
        <TabPanel value={value} index={4}>
          <LunchCategory foodUrl={process.env.REACT_APP_API +"/menu/get/lunch/friday"} />{" "}
        </TabPanel>
      
 
      </div>
    </MenuItemsContainer>
    </>
  );
}

export default LunchContainer;
