import React, { useState,useEffect } from "react";
import { useHistory } from "react-router-dom";
import styled from "styled-components";
import Button from "@material-ui/core/Button";
import { Link } from "react-router-dom";
import Select from "react-select";
import InputLabel from "@material-ui/core/InputLabel";
import TextField from "@material-ui/core/TextField";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";
import axios from 'axios';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faEdit, faTrash } from "@fortawesome/free-solid-svg-icons";
import FormControl from "@material-ui/core/FormControl";
import { makeStyles } from "@material-ui/core/styles";
require('dotenv').config()
const OfferPageContainer = styled.div`
  max-height: 100vh;
  height: 100vh;
  width: 100%;
  position: relative;

  .MuiButton-containedPrimary {
    width: auto;
    margin: 1rem 0;
    background-color: #40b65f;
  }
  .MuiButton-containedPrimary:hover {
    background-color: #2a9432;
  }
  .MuiButton-containedSecondary {
    width: auto;
    margin: 1rem 0;
    background-color: #dc3441;
  }
  .MuiButton-containedSecondary:hover {
    background-color: #b2252f;
  }
  .MuiTableCell-head {
    font-weight: 700;
    color:#5e5b5b;

  }

  .back-btn {
    text-decoration: none;
  }
`;

const ContentWrapper = styled.div`
  width: auto;
  height: 100%;
  margin-left: 16rem;
  display: flex;
  flex-direction:column;
  align-items: flex-start;
  padding:10rem 1.5rem;
  h2{
    margin:1rem 0;
    color:#5e5b5b;
  }
`;

const MenuItemSelector = styled.div`
  width: 210px;
`;

const PureLayer = styled.div`
  width: 30%;
  height: 60%;
  position: absolute;
  left: 60%;
  top: 50%;
  transform: translate(-60%, -50%);
  z-index: 5;
  background: linear-gradient(
    312deg,
    rgba(171, 171, 171, 1),
    rgba(255, 255, 255, 1)
  );
  box-shadow: 3px 3px 15px #aaaaaa;
  border: 1px solid green;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  h1 {
    color: #268b93;
  }
`;
const BtnContainer = styled.div`
  width: 100%;
  display: flex;
  justify-content: space-around;
`;

const BacktoOffer = styled.span`
  position: absolute;
  left: 20%;
  top: 5%;
`;
const AddOfferSection = styled.span`
  position: absolute;
  right: 5%;
  top: 5%;
  color: #40b65f;
  font-size: 2rem;
  cursor: pointer;
`;
const ActionItem = styled.div`
  display: flex;
  justify-content: flex-end;
  align-items: center;
  font-size: 1.1rem;
  cursor: pointer;
  .edit {
    margin: 0 0.5rem;
    color: #43ca53;
    text-decoration: none;
  }
  .delete {
    color: #d33e23;
  }
`;




const useStyles = makeStyles({
  table: {
    minWidth: 750,
  },
});

const OfferEntry = (props) => {

  const history = useHistory();
  if(!props.location.offerProps){
    history.push("/admin/offers");
}
else{
 // var menuoptions =  props.location.offerProps.menuoptions;
  //console.log(props.location.offerProps.menuoptions);
 
   const menu_url = process.env.REACT_APP_API +"/menu"
   var menuoptions = [];
    
   
    axios.get(menu_url).then((response) => {
       
      if (response.data) {

     
        for (var result in response.data){
          
          menuoptions.push({
            value: response.data[result].id,
            label: response.data[result].name
          });
        }
        
      } else {
      }
    });
  
}

 
  const[selectedmenu, setSelectedmenu] = useState(null);
  const[disprice, setdisprice] = useState(null);
  const[itemid,setItemid] = useState(null);
 

  const [offeritems, setOfferItems] = useState(null);
 


  const  handleChange = (selectedmenu) => {
   
    try{
    setSelectedmenu(selectedmenu.value);}
  
  catch(e){
    console.log(e.message);
  }
   
  }
  
 
  const classes = useStyles();

  const fetchData = async () => {
    const url = process.env.REACT_APP_API +"/offer/"+props.location.offerProps.offerid;

    axios.get(url).then((response) => {
      if (response.data) {
        
       
        setOfferItems(response.data);
      } else {
      }
    });
  };

  const handleDelete = (id) => {
    const url = process.env.REACT_APP_API +"/offer/"+props.location.offerProps.offerid+"/"+id;

    axios.delete(url)
    .then((response) => {

      alert(response.data);
      if(props.location.offerProps){
        fetchData();
    }
     
    })
    .catch((error) => {});
  };

  
  function handleNewAdd() {
    
    var disprice = document.getElementById("discount").value;


    if (itemid) {
     
      
      const url = process.env.REACT_APP_API +"/offer/"+props.location.offerProps.offerid+"/"+itemid;
    
  
      axios.put(url, {menu_id: selectedmenu, disprice: disprice})
      .then((response) => {

        alert(response.data);
        if(props.location.offerProps){
          fetchData();
      }
       
      })
      .catch((error) => {});
    } else {
      
      const url = process.env.REACT_APP_API +"/offer/"+props.location.offerProps.offerid;
    
      axios.post(url, {menu_id: selectedmenu, disprice: disprice})
      .then((response) => {

        alert(response.data);
        if(props.location.offerProps){
          fetchData();
      }
       
      })
      .catch((error) => {});
    }
    closeModal();
   
  }

  const editmodal = (id,menuid, disprice) => {
    setdisprice(disprice);
    setSelectedmenu(menuid);
    setItemid(id);
    setShowForm(true);
    
  };

  function closeModal() {
    setdisprice(null);
    setSelectedmenu(null);
    setItemid(null);
    setShowForm(false);
  }

  useEffect(() => {
    if(props.location.offerProps){
      fetchData();
  }
   
  
    
  }, [2]);
  
  //const offer = props.location.state;

  const [showForm, setShowForm] = useState(false);
  const [isClearable, setClearable] = useState(true);
  const [isSearchable, setSearchable] = useState(true);

  const toggleClearable = () => setClearable(!isClearable);

  const toggleSearchable = () => setSearchable(!isSearchable);


  const toggleForm = () => setShowForm(!showForm);
  return (
    <OfferPageContainer>
      <AddOfferSection>
        <i
          className={showForm ? "" : "fas fa-plus-square"}
          onClick={toggleForm}
        ></i>
      </AddOfferSection>
      <ContentWrapper>
        {showForm ? (
          <PureLayer>
            <h1>Add/Edit Offers</h1>
            <MenuItemSelector>
              <InputLabel htmlFor="outlined-age-native-simple">
                Menu Items:
              </InputLabel>
              <Select
              value={menuoptions.find(op => {
                return op.value === selectedmenu
             })}
                onChange={handleChange}
                id="menuid"
                className="basic-single"
                classNamePrefix="select"
                
                isDisabled={false}
                isLoading={false}
                isClearable={isClearable}
                isRtl={false}
                isSearchable={isSearchable}
                name="Menu Items"
                options={menuoptions}
                placeholder="Choose Menu Item..."
              />
            </MenuItemSelector>
            <TextField
              id="discount"
              label="Discount Amount"
              variant="outlined"
              size="small"
              defaultValue={disprice}
            />
            <BtnContainer>
              <Button variant="contained" color="primary" onClick={handleNewAdd}>
                Submit
              </Button>
              <Button
                variant="contained"
                color="secondary"
                onClick={closeModal}
              >
                Close
              </Button>
            </BtnContainer>
          </PureLayer>
        ) : (
          ""
        )}
        <>
       

        <h2>Offer Items</h2>
        <TableContainer component={Paper}>
          <Table aria-label="simple table">
            <TableHead>
              <TableRow>
                <TableCell>Item Name</TableCell>
                <TableCell align="right">Orginal Price</TableCell>
                <TableCell align="right">Discounted Price</TableCell>
                
                <TableCell align="right">Action</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
             {offeritems &&
              offeritems.map((offeritems, index) => {
                return (
                  <TableRow key={index}>
                    <TableCell component="th" scope="row">
                      {offeritems.name}
                    </TableCell>
                    <TableCell align="right">{offeritems.price}</TableCell>
                    <TableCell align="right">{offeritems.disprice}</TableCell>
                    <TableCell align="right">
                      <ActionItem>
                        <div className="edit">
                          
                            <FontAwesomeIcon
                               onClick={() => editmodal(offeritems.id,offeritems.menu_id,offeritems.disprice)}
                              icon={faEdit}
                            />
                         
                        </div>
                        <div className="delete">
                          <FontAwesomeIcon
                            icon={faTrash}
                            onClick={() => handleDelete(offeritems.id)}
                          />
                        </div>
                      </ActionItem>
                    </TableCell>
                  </TableRow>
                );
              })}
            </TableBody>
          </Table>
        </TableContainer>
        </>
      </ContentWrapper>
      <BacktoOffer>
        <Link to="/admin/offers" className="back-btn">
          <Button variant="contained" color="primary">
            Back to Offers
          </Button>
        </Link>
      </BacktoOffer>
    </OfferPageContainer>
  );
};

export default OfferEntry;
