import React, {Component} from 'react';
import Login from './components/Login';
import Logout from './components/Logout';
import Dashboard from "./containers/Dashboard";
import {BrowserRouter,Switch, Route} from 'react-router-dom';
import Welcome from "./components/Welcome";
import MenuContainer from "./components/MenuContainer";
import LunchContainer from "./components/LunchContainer";
import Offers from "./components/Offers";
import Time from "./components/Time";
import OfferEntry from "./components/OfferEntry";
function App()  {
  return (
    <BrowserRouter>
    <Switch>
      <Route exact path="/" component={Login} />
      <Route  path="/admin" component={Dashboard} >
      <Route exact path={["/admin", "/admin/dashboard"]} component={Welcome} />
            <Route path="/admin/menuItems" component={MenuContainer} />
            <Route path="/admin/lunch"  component={LunchContainer} />
            <Route path="/admin/offers" component={Offers} />
            <Route path="/admin/time" component={Time} />
            <Route path="/admin/offerentry" component={OfferEntry}/>
            </Route>
      <Route  path ="/logout" component={Logout} />
  </Switch>
  </BrowserRouter>
  );
}
export default App;

