import React, { useState, useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";
import Button from "@material-ui/core/Button";
import styled from "styled-components";
import axios from "axios";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faEdit, faTrash } from "@fortawesome/free-solid-svg-icons";
import Modal from "react-modal";
import TextField from "@material-ui/core/TextField";
import InputLabel from "@material-ui/core/InputLabel";
import Sidebar from "../components/Sidebar/Sidebar";
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css'; // Import css

import { css } from "@emotion/core";
import PulseLoader from "react-spinners/PulseLoader";

require('dotenv').config()

// Can be a string as well. Need to ensure each key-value pair ends with ;
const override = css`
  display: block;
 
  margin: auto;
  transform: "translate(-50%, -50%)",

  
 
`;

const customStyles = {
  content: {
    top: "50%",
    left: "50%",
    right: "auto",
    bottom: "auto",
    marginRight: "-50%",
    transform: "translate(-50%, -50%)",
  },
};

const MenuItemsContainer = styled.div`
  width: auto;
  height: 100vh;
  display: flex;
  flex-direction: column;
  margin-top: 5rem;
  align-items: start;
  margin-left: 16rem;
  padding: 1rem;

  .MuiButton-containedPrimary {
    width: 150px;
    margin: 1rem 0;
    background-color: #40b65f;
  }
  .MuiButton-containedPrimary:hover {
    background-color: #2a9432;
  }
  .MuiTableCell-head {
    font-weight: 700;
    color:#5e5b5b;

  }
`;
const ActionItem = styled.div`
  display: flex;
  justify-content: flex-end;
  align-items: center;
  font-size: 1.1rem;
  cursor: pointer;
  .edit {
    margin: 0 0.5rem;
    color: #43ca53;
    text-decoration: none;
  }
  .delete {
    color: #d33e23;
  }
`;
const FormContainer = styled.div`
  form {
    padding: 2.5rem;
    display: flex;
    flex-direction: column;
  }
  .MuiFormControl-root {
    border: 0;
    margin: 0.8rem 0;
    display: inline-flex;
    padding: 0;
    position: relative;
    min-width: 0;
    flex-direction: column;
    vertical-align: top;
  }
`;
const BtnContainer = styled.div`
  display: flex;
  justify-content: space-around;
`;

const useStyles = makeStyles({
  table: {
    minWidth: 750,
  },
});

export default function Time() {
  const [time, setTime] = useState(null);
  const url = process.env.REACT_APP_API +"/time";

  const fetchData = async () => {
    axios.get(url).then((response) => {
      if (response.data) {
        setTime(response.data);
        
      } else {
      }
    });
  };

  useEffect(() => {
    fetchData();
  }, [2]);

  var subtitle;
  const [modalIsOpen, setIsOpen] = React.useState(false);
  const [day, setday] = useState(null);
  const [id, setid] = useState(null);
  const [stime, setStime] = useState(null);
  const [etime, setEtime] = useState(null);
  const [type, settype] = useState(null);
  let [loading, setLoading] = useState(false);
  const [dayError, setDayError] = useState(null);
  const [ohourError, setOhourError] = useState(null);
  const [ehourError, setEhourError] = useState(null);
  const [typeError, setTypeError] = useState(null);
 
  function openModal() {
    setIsOpen(true);
  }

  function afterOpenModal() {
    // references are now sync'd and can be accessed.
    subtitle.style.color = "#268B93";
  }

  function closeModal() {
    setIsOpen(false);
    setday(null);
    setStime(null);
    setEtime(null);
    settype(null)
    setid(null);
    setDayError(null);
    setOhourError(null);
    setEhourError(null);
    setTypeError(null);
  }



  const editmodal = (id, day, stime, etime,type) => {
    setday(day);
    setStime(stime);
    setEtime(etime);
    setid(id);
    settype(type);
   
    openModal();
  };

  function validate(){
    const day = document.getElementById("day").value;
    const ohour = document.getElementById("ohour").value;
    const ehour = document.getElementById("ehour").value;
    const type = document.getElementById("type").value;
   
    if(!day){
      setDayError("This field is required.")
    }else{
      setDayError(null);
    }
    if(!ohour){
      setOhourError("This field is required.")
    }else{
      setOhourError(null);
    }
    if(!ehour){
      setEhourError("This field is required.")
    }else{
      setEhourError(null);
    }
    if(!type){
      setTypeError("This field is required.")
    }else{
      setTypeError(null);
    }
    if(day && ohour && ehour && type){
      setLoading(true);
      handleNewAdd();
    }
   
  }
  function handleNewAdd() {
    
    const day = document.getElementById("day").value;
    const ohour = document.getElementById("ohour").value;
    const ehour = document.getElementById("ehour").value;
    const type = document.getElementById("type").value;
    const url = process.env.REACT_APP_API +"/time";

   
    if (id) {
      axios.put(url+"/"+id, { day: day, starttime: ohour, endtime: ehour , type:type})
      .then((response) => {

        
        confirmAlert({
          title: 'Response messge',
          message: response.data,
          buttons: [
            {
              label: 'Okay',
              onClick: () => null
            }
           
          ]
        });
         fetchData();
        setLoading(false);
      })
      .catch((error) => {
        setLoading(false);
        console.log(error.message);
      });
      
      
     
    } else {
      axios.post(url, { day: day, starttime: ohour, endtime: ehour , type:type})
      .then((response) => {

        
        confirmAlert({
          title: 'Response messge',
          message: response.data,
          buttons: [
            {
              label: 'Okay',
              onClick: () => null
            }
           
          ]
        });
        fetchData();
        setLoading(false);
      })
      .catch((error) => {
        setLoading(false);
        console.log(error.message);
      });
      
      
    }

    closeModal();
  }

  const confirmDelete = (id) => {
    setLoading(true);
    const url = process.env.REACT_APP_API +"/time/"+id;
    axios.delete(url).then((response) => {
      setLoading(false);
      confirmAlert({
        title: 'Response messge',
        message: response.data,
        buttons: [
          {
            label: 'Okay',
            onClick: () => null
          }
         
        ]
      });
      
      fetchData();
     
    })
    .catch((error) => {
      setLoading(false);
     
    });


  }
  const handleDelete = (id) => {

    confirmAlert({
      title: 'Confirm to submit',
      message: 'Are you sure to delete this.',
      buttons: [
        {
          label: 'Yes',
          onClick: () => confirmDelete(id)
        },
        {
          label: 'No',
          onClick: () => null
        }
      ]
    });
   
  };

  const classes = useStyles();

  return (
    <>
    <Sidebar />
    <MenuItemsContainer>
      <h1>Time</h1>
      <br />
      <Button variant="contained" color="primary" onClick={openModal}>
        Add Time
      </Button>
      <Modal
        isOpen={modalIsOpen}
        onAfterOpen={afterOpenModal}
        onRequestClose={closeModal}
        style={customStyles}
        contentLabel="Time Modal"
      >
        <h2
          ref={(_subtitle) => (subtitle = _subtitle)}
          style={{ textAlign: "center" }}
        >
          Add/Edit Time
        </h2>
        <FormContainer>
          <form>
            <InputLabel htmlFor="outlined-age-native-simple">Day </InputLabel>
            <div style={{ color:"red"}}>{dayError}</div>
            <TextField id="day" defaultValue={day} variant="outlined" />
           
            <InputLabel htmlFor="outlined-age-native-simple">
              Opening Hour:{" "}
            </InputLabel>
            <div style={{ color:"red"}}>{ohourError}</div>
            <TextField
              id="ohour"
              defaultValue={stime}
              variant="outlined"
              
            />
            <InputLabel htmlFor="outlined-age-native-simple">
              Closing Hour:{" "}
            </InputLabel>
            <div style={{ color:"red"}}>{ehourError}</div>
            <TextField
              id="ehour"
              defaultValue={etime}
              variant="outlined"
              
            />

       <FormControl variant="outlined" className={classes.formControl}>  
              <InputLabel htmlFor="outlined-age-native-simple">
                Type
              </InputLabel>
              <div style={{ color:"red"}}>{typeError}</div>
              <Select native id="type" defaultValue={type} label="Status">
                <option aria-label="None" value="" />
                <option value="lunch">Lunch</option>
                <option value="ala-carte">Ala-Carte</option>
              </Select>
            </FormControl>
          </form>
        </FormContainer>
        <BtnContainer>
          {" "}
          <Button variant="contained" color="primary" onClick={validate}>
            Submit
          </Button>
          <Button variant="contained" color="secondary" onClick={closeModal}>
            close
          </Button>
        </BtnContainer>
      </Modal>
      <TableContainer component={Paper}>
      <PulseLoader color={'#43ca53'} loading={loading} css={override} size={15} />

        <Table className={classes.table} aria-label="simple table">
          <TableHead>
            <TableRow>
              <TableCell>Day</TableCell>
              <TableCell align="right">Start Time</TableCell>
              <TableCell align="right">End Time</TableCell>
              <TableCell align="right">Type</TableCell>
              <TableCell align="right">Action</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {time &&
              time.map((time, index) => {
                return (
                  <TableRow key={time.day} key={index}>
                    <TableCell component="th" scope="row">
                      {time.day}
                    </TableCell>
                    <TableCell align="right">{time.starttime}</TableCell>
                    <TableCell align="right">{time.endtime}</TableCell>
                    <TableCell align="right">{time.type}</TableCell>
                    <TableCell align="right">
                      <ActionItem>
                        <div className="edit">
                          <FontAwesomeIcon
                            icon={faEdit}
                            onClick={() =>
                              editmodal(
                                time.id,
                                time.day,
                                time.starttime,
                                time.endtime,
                                time.type
                              )
                            }
                          />
                        </div>
                        <div className="delete">
                          <FontAwesomeIcon
                            icon={faTrash}
                            onClick={() => handleDelete(time.id)}
                          />
                        </div>
                      </ActionItem>
                    </TableCell>
                  </TableRow>
                );
              })}
          </TableBody>
        </Table>
      
      </TableContainer>

     
    </MenuItemsContainer>
    </>
  );
}
