import React, { Component } from "react";
import {Redirect} from 'react-router-dom';
import styled from "styled-components";
import TextField from "@material-ui/core/TextField";
import OpenSoon from "../assets/images/OpenSoon.jpg";
import Button from "@material-ui/core/Button";
import Vegpoint from "../assets/logos/logo1.png";
import axios from "axios";

require('dotenv').config()
const SchoolLoginPageContainer = styled.div`
  width: 100%;
  height: 100vh;
  background: linear-gradient(
      to top,
      rgba(0, 0, 0, 0.8) 10%,
      rgba(0, 0, 0, 0.5) 80%
    ),
    url(${OpenSoon}) center/cover no-repeat border-box;
  box-sizing: border-box;
  position: relative;
  overflow-y: hidden;
`;

const LoginBox = styled.div`
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 360px;
  form {
    background: rgba(255, 255, 255, 0.9);
    padding: 1.5rem;
    height: auto;
    min-height: 40vh;
    border-radius: 16px;
    border-left: 1px solid rgba(255, 255, 255, 0.3);
    border-top: 1px solid rgba(255, 255, 255, 0.3);
    box-shadow: 20px 20px 40px -6px rgba(0, 0, 0, 0.2);
    transition: all -0.2s ease;
    display: flex;
    flex-direction: column;

    img {
      width: 150px;
      height: auto;
      margin: 1rem auto;
    }
  }

  .error {
    color: red;
  }
`;



export default class Login extends Component {

  constructor(props){
    super(props);
    const token = localStorage.getItem("token")
    let loggedIn = true;
    if(token ==null){
      loggedIn = false
    }
    this.state = {
      username:'',
      password:'',
      error:'',
      loggedIn
    }
    this.onChange = e => this.onChange.bind(this);
    //this.submitForm = e =>this.submitForm.bind(this);
  }

 

  submitForm= (e) =>{
    e.preventDefault();
 
    const {username,password} = this.state;
    //login magic
    const url = process.env.REACT_APP_API + '/auth'
      
    axios.post(url, {'username': username, 'password':password})
   .then((response) => {
    
  if (response.data === 'success') {
      localStorage.setItem("token","success");
      this.setState({
        loggedIn: true
      })

  } else {
    this.setState({
      error:'username or password does not match'})
  }
 
});
   
  };
 render() {
   if(this.state.loggedIn){
     return <Redirect to="/admin" />
   }
  return (
    <div className="wholeContainer">

          <SchoolLoginPageContainer>
            <LoginBox>
              <form className="form" onSubmit={this.submitForm}>
                <img src={Vegpoint} />
                <div>&nbsp;</div>
                {this.state.error && <p className="error">{this.state.error} </p>}
                <div>&nbsp;</div>
                <TextField
                  id="outlined-basic"
                  label="username"
                  variant="outlined"
                  type="text"
                  name="username"
                  value={this.state.username}
                 
                  placeholder="Enter username"
                  
                  autoFocus
                  onChange={e => this.setState({ username: e.target.value })}
                  
                />
                <div>&nbsp;</div>
                <TextField
                  id="outlined-basic"
                  label="password"
                  variant="outlined"
                  type="password"
                  placeholder="Enter password"
                  name="password"
                  value={this.state.password}
                  onChange={e => this.setState({ password: e.target.value })}
                />
                <div>&nbsp;</div>

                <Button
                  type="submit"
                  variant="contained"
                  color="secondary"
                 
                >
                 Log In
                </Button>
              </form>
            </LoginBox>
          </SchoolLoginPageContainer>
     </div>
  )
}
}

