import React, { useState } from "react";
import styled from "styled-components";
import { makeStyles } from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import axios from "axios";
import TextField from "@material-ui/core/TextField";
import TextareaAutosize from "@material-ui/core/TextareaAutosize";
import Select from "@material-ui/core/Select";
import InputLabel from "@material-ui/core/InputLabel";
import FormControl from "@material-ui/core/FormControl";
import Modal from "react-modal";
import PropTypes from "prop-types";
import AppBar from "@material-ui/core/AppBar";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import Typography from "@material-ui/core/Typography";
import Box from "@material-ui/core/Box";
import MenuCategory from "./Menu/MenuCategory";
import Sidebar from "../components/Sidebar/Sidebar";

import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css'; // Import css
import { css } from "@emotion/core";
import PulseLoader from "react-spinners/PulseLoader";
require('dotenv').config()
// Can be a string as well. Need to ensure each key-value pair ends with ;
const override = css`
  display: block;
 
  margin: auto;
  transform: "translate(-50%, -50%)",

  
 
`;

const MenuItemsContainer = styled.div`
  width: auto;
  height: 100vh;
  max-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-left: 16rem;
  padding: 1rem;

  .MuiTableCell-head {
    font-weight: 700;
    color:#5e5b5b;

  }

  .MuiButton-containedPrimary {
    width: 150px;
    margin: 1.5rem 0;
    background-color: #40b65f;
  }
  .MuiButton-containedPrimary:hover {
    background-color: #2a9432;
  }
  .MuiAppBar-colorPrimary {
    background-color: #40b65f;
  }
  .mui-tab {
    width: 100%;
  }
`;

const FormContainer = styled.div`
  form {
    padding: 2.5rem;
    display: flex;
    flex-direction: column;
  }
  #desc {
    border: 1px solid #b8b8b8;
    border-radius: 4px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    font-family: "Roboto";
    font-size: 1rem;
    padding-left: 0.8rem;
    padding-top: 0.5rem;
  }
  .MuiFormControl-root {
    border: 0;
    margin: 1rem 0;
    display: inline-flex;
    padding: 0;
    position: relative;
    min-width: 0;
    flex-direction: column;
    vertical-align: top;
  }
`;

const BtnContainer = styled.div`
  display: flex;
  justify-content: space-around;
`;

const customStyles = {
  content: {
    top: "50%",
    left: "50%",
    right: "auto",
    bottom: "auto",
    marginRight: "-50%",
    transform: "translate(-50%, -50%)",
  },
};

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}>
      {value === index && (
        <Box p={3}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    "aria-controls": `simple-tabpanel-${index}`,
  };
}

function MenuContainer() {
  const [modalIsOpen, setIsOpen] = React.useState(false);
  let [loading, setLoading] = useState(false);
  const [pic, setpic] = useState(null);
  const [typeError, setTypeError] = useState(null);
  const [nameError, setnameError] = useState(null);
  const [desc_enError, setdesc_enError] = useState(null);
  const [desc_fiError, setdesc_fiError] = useState(null);
  const [priceError, setpriceError]= useState(null);
  const [picError, setpicError]= useState(null);

  function openModal() {
    setIsOpen(true);
  }

  function closeModal() {
    setIsOpen(false);
    setnameError(null);
    setdesc_enError(null);
    setdesc_fiError(null);
    setTypeError(null);
    setpicError(null);
    setpriceError(null);
  }


  function validate(){
    const name = document.getElementById("name").value;
    const price = document.getElementById("price").value;
    const desc_en = document.getElementById("desc_en").value;
    const desc_fi = document.getElementById("desc_fi").value;
    const type = document.getElementById("type").value;

    if(!name){
      setnameError("This field is required.")
    }else{
      setnameError(null);
    }
    if(!price){
      setpriceError("This field is required.")
    }else{
      setpriceError(null);
    }
    if(!desc_en){
      setdesc_enError("This field is required.")
    }else{
      setdesc_enError(null);
    }
    if(!desc_fi){
      setdesc_fiError("This field is required.")
    }else{
      setdesc_fiError(null);
    }
    if(!type){
      setTypeError("This field is required.")
    }else{
      setTypeError(null);
    }

    if(!pic){
      setpicError("This field is required.")
    }else{
      setpicError(null);
    }
    if(name && desc_en && desc_fi && type && pic && price){
      setLoading(true);
      handleAdd();
    }
   
  }
  function handleAdd() {
    const name = document.getElementById("name").value;
    const desc_en = document.getElementById("desc_en").value;
    const desc_fi = document.getElementById("desc_fi").value;
    const price = document.getElementById("price").value;
    const type = document.getElementById("type").value;
    const subtype = document.getElementById("subtype").value;
   

    const url =  process.env.REACT_APP_API +"/menu";

    const config = {
      headers: {
        "content-type": "multipart/form-data",
      },
    };
    const formData = new FormData();
    formData.append("name", name);
    formData.append("price", price);
    formData.append("description_en", desc_en);
    formData.append("description_fi", desc_fi);
    formData.append("type", type);
    formData.append("subtype",subtype)
    formData.append("pic", pic);
    
    

    axios
      .post(url, formData, config)
      .then((response) => {

        confirmAlert({
          title: 'Response messge',
          message: response.data,
          buttons: [
            {
              label: 'Okay',
              onClick: () => null
            }
           
          ]
        });
        
        setLoading(false);
      })
      .catch((error) => {});
  
    closeModal();
  }

  const fileSelectedHandler = (event) => {
    setpic(event.target.files[0]);
  };

  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };
  return (

    <>
    <Sidebar />
    <MenuItemsContainer>
      <h1>Menu Items</h1>
      <br />
      <Button variant="contained" color="primary" onClick={openModal}>
        Add Items
      </Button>
      <Modal
        isOpen={modalIsOpen}
        onRequestClose={closeModal}
        style={customStyles}
        contentLabel="Example Modal"
      >
        <h2 style={{ textAlign: "center", color: "#268B93" }}>Add Menu Item</h2>
        <FormContainer>
          <form enctype="multipart/form-data">
          <div style={{ color:"red"}}>{nameError}</div>
            <TextField id="name" label="Item Name" variant="outlined" />
            <div style={{ color:"red"}}>{desc_enError}</div>
            <TextareaAutosize
              id="desc_en"
              aria-label="minimum height"
              rowsMin={5}
              placeholder="Item Description in English"
            />
            <div style={{ color:"red"}}>{desc_fiError}</div>
            <TextareaAutosize
              id="desc_fi"
              aria-label="minimum height"
              rowsMin={5}
              placeholder="Item Description in Fi"
            />
             <div style={{ color:"red"}}>{priceError}</div>
            <TextField
              id="price"
              label="Price"
              variant="outlined"
              type="number"
            />
            <FormControl variant="outlined">
              <InputLabel htmlFor="outlined-age-native-simple">
                Type:
              </InputLabel>
              <div style={{ color:"red"}}>{typeError}</div>
              <Select native id="type" label="category">
                
                <option value="soup">Soup</option>
                <option value="starters">Appetizer</option>
                <option value="tandooridish">Tandoori Dishes</option>
                <option value="maindish">Main Dishes</option>
                <option value="special">Chef's Special</option>
                <option value="snacks">Snacks</option>
                <option value="rice">Rice</option>
                <option value="bread">Breads</option>
                <option value="beverage">Beverages</option>
                <option value="dessert">Desserts</option>
                <option value="kids">For Kids</option>
              </Select>
            </FormControl>

            <FormControl variant="outlined">
              <InputLabel htmlFor="outlined-age-native-simple">
                SubType:
              </InputLabel>
              <Select native id="subtype" label="category">
                <option aria-label="None" value="" />
                <option value="hot_drink">Hot Drink</option>
                <option value="cold_drink">Cold Drink</option>
               
              </Select>
            </FormControl>

           

            <InputLabel htmlFor="outlined-age-native-simple">Icon:</InputLabel>{" "}
            <div style={{ color:"red"}}>{picError}</div>
            <TextField
              id="pic"
              variant="outlined"
              type="file"
              onChange={fileSelectedHandler}
            />
          </form>
        </FormContainer>

        <BtnContainer>
          {" "}
          <Button variant="contained" color="primary" onClick={validate}>
            Submit
          </Button>
          <Button variant="contained" color="secondary" onClick={closeModal}>
            close
          </Button>
        </BtnContainer>
      </Modal>

      <div className="mui-tab">
      <PulseLoader color={'#43ca53'} loading={loading} css={override} size={15} />

        <AppBar position="static">
          <Tabs
            value={value}
            onChange={handleChange}
            aria-label="simple tabs example"
            variant="scrollable"
            scrollButtons="auto"
          >
            <Tab label="Soup" {...a11yProps(0)} />
            <Tab label="Appetizer" {...a11yProps(1)} />
            <Tab label="Tandoori Dishes" {...a11yProps(2)} />
            <Tab label="Main Dishes" {...a11yProps(3)} />
            <Tab label="Chef's Special" {...a11yProps(4)} />
            <Tab label="Snacks" {...a11yProps(5)} />
            <Tab label="Rice" {...a11yProps(6)} />
            <Tab label="Breads" {...a11yProps(7)} />
            <Tab label="Beverages" {...a11yProps(8)} />
            <Tab label="Desserts" {...a11yProps(9)} />
            <Tab label="For Kids" {...a11yProps(10)} />
          </Tabs>
        </AppBar>
        <TabPanel value={value} index={0}>
          <MenuCategory foodUrl={process.env.REACT_APP_API +"/menu/soup"} />
        </TabPanel>
        <TabPanel value={value} index={1}>
          <MenuCategory foodUrl={process.env.REACT_APP_API +"/menu/appetizer"} />{" "}
        </TabPanel>
        <TabPanel value={value} index={2}>
          <MenuCategory foodUrl={process.env.REACT_APP_API +"/menu/tandooridish"} />{" "}
        </TabPanel>
        <TabPanel value={value} index={3}>
          <MenuCategory foodUrl={process.env.REACT_APP_API +"/menu/main"} />{" "}
        </TabPanel>
        <TabPanel value={value} index={4}>
          <MenuCategory foodUrl={process.env.REACT_APP_API +"/menu/special"} />{" "}
        </TabPanel>
        <TabPanel value={value} index={5}>
          <MenuCategory foodUrl={process.env.REACT_APP_API +"/menu/snacks"} />{" "}
        </TabPanel>
        <TabPanel value={value} index={6}>
          <MenuCategory foodUrl={process.env.REACT_APP_API +"/menu/rice"} />{" "}
        </TabPanel>
        <TabPanel value={value} index={7}>
          <MenuCategory foodUrl={process.env.REACT_APP_API +"/menu/bread"} />{" "}
        </TabPanel>
        <TabPanel value={value} index={8}>
          <MenuCategory foodUrl={process.env.REACT_APP_API +"/menu/beverage"} />{" "}
        </TabPanel>
        <TabPanel value={value} index={9}>
          <MenuCategory foodUrl={process.env.REACT_APP_API +"/menu/dessert"} />{" "}
        </TabPanel>
        <TabPanel value={value} index={10}>
          <MenuCategory foodUrl={process.env.REACT_APP_API +"/menu/kids"} />{" "}
        </TabPanel>
      </div>
    </MenuItemsContainer>
    </>
  );
}

export default MenuContainer;
