import React ,{useReducer} from "react";
import styled from "styled-components";
import MenuItem from "./MenuItem";
import { AiOutlineHome } from "react-icons/ai";
import { AiOutlineInfoCircle } from "react-icons/ai";
import { FiBookOpen } from "react-icons/fi";
import { IoNewspaperOutline } from "react-icons/io5";
import { Link,useHistory } from "react-router-dom";



const MenuContainer = styled.div`
  margin-top: 1rem;
  width: 100%;

  .menu-link {
    text-decoration: none;
  }
`;

const Menu = () => {

 

  return (
    <MenuContainer>
      <Link to="/admin/dashboard" className="menu-link">
        <MenuItem title="Home" icon={<AiOutlineHome />} active />
      </Link>
      <Link to="/admin/menuItems" className="menu-link">
        <MenuItem title="Menu Items" icon={<AiOutlineInfoCircle />} />
      </Link>
      

      <Link to="/admin/lunch" className="menu-link">
        <MenuItem title="Lunch Items" icon={<AiOutlineInfoCircle />} />
      </Link>

      <Link to="/admin/offers" className="menu-link">
        <MenuItem title="Offers" icon={<FiBookOpen />} />
      </Link>
      <Link to="/admin/time" className="menu-link">
        <MenuItem title="Time" icon={<IoNewspaperOutline />} />
      </Link>

     
      <Link to="/logout" className="menu-link">
        <MenuItem title="Logout" icon={<IoNewspaperOutline />} />
      </Link>

      
     
      
    </MenuContainer>
  );
};

export default Menu;
